* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  color: white;
  text-decoration: none;
}

.navbar {
  width: 100%;
  height: 10vh;
  background: black;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.footer {
  width: 100%;
  height: 10vh;
  background: black;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.book-list {
min-height: 80vh;
width: 100%;
display: grid;
grid-template-columns: repeat(3, 1fr);
justify-items: center;
text-align: center;
}

.favourites {
min-height: 80vh;
width: 100%;
display: grid;
grid-template-columns: repeat(3, 1fr);
justify-items: center;
text-align: center;
}

.book-details {
  min-height: 80vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  /* text-align: center; */
  padding: 10px;
}

.book-description {
  margin: 10x 10px;
}

img {
  width: 300px;
  height: 300px;
  transition: all 0.3s ease;
}

img:hover {
  transform: scale(1.1);
}

.book {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px 0 ;
}

button {
  padding: 10px;
  background-color: black;
  color: white;
  width: 300px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
}